import React from 'react';
import HeaderComponent from "../components/Header/header";
import FooterComponent from "../components/Footer/footer";
import DogComponent from "../components/Dog/dog";
import { CardDeck, Card } from 'react-bootstrap';
import { withRouter } from 'react-router-dom';
import fakeDogs from '../FakeDataBase';
import Map from "../components/GoogleMap/map";

class Dogs extends React.Component {
  constructor(props) {
    super(props);
    this.state = { 
      zip:[],
      dogs:[]
    };
  }

  componentDidMount() {
    //access props history with the next line.
    const state = this.props.location.state
    this.setState({
      dogs: this.props.location.state.dogs,
      zip: this.props.location.search
    });
  }

  UNSAFE_componentWillMount() {
    this.setState({ results: this.createDogCardDecks()});
  }

  render() {
    console.log(this.state.dogs);
    console.log(this.state.zip);
    this.createDogCardDecks()
    return (
      <div className="dog-page">
        <div className="myHeader">
         <HeaderComponent />
        </div>
        <div className="myDogs">
          {this.state.results}
        </div>
        <div>
          <Map></Map>
        </div>
        <div className="myFooter">
          <FooterComponent />
        </div>
      </div>
    );
  }

  createDogCardDecks = () => {
    var dogs = this.state.dogs
    console.log(dogs[0])
    let table = []
    let k = 0
    for (let i = 0; i < dogs.length; i++) {
      let children = []
      for (let j = 0; j < 3; j++) {
        if(dogs[k])
          children.push(<DogComponent 
            key={k}
            dogsId={k} 
            name={dogs[k].name} 
            age={dogs[k].age} 
            breed={dogs[k].breed} image={dogs[k].image}></DogComponent>)
        else
          children.push(<Card></Card>)
        k++
        i=k-1;
      }
      table.push(<CardDeck>{children}</CardDeck>)
    }
    return table
  }
  
}

export default withRouter(Dogs);